.signin-button {
    background-color: green;
    border: none;
    color: white;
    padding: 16px 32px;
    text-align: center;
    display: inline-block;
    cursor: pointer;
    font-size: 1.25rem;
    border-radius: 30px;
}

.signin-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}